<template>
        <client-only>
                <el-tooltip :trigger="isMobile ? 'click' : 'hover'" :show-after="300" effect="dark" :content="contentStr" :placement="isMobile ? 'bottom' : 'right'" append-to=".challenges" popper-class="aTips">
                        <span class="sp">
                                {{ valueStr }}
                                <img class="icon" alt="" src="@/assets/images/challenges/doubt.svg" />
                        </span>
                </el-tooltip>
        </client-only>
</template>
<script setup>
import { useCheckMobile } from '@/store/isMobile'
const isMobile = computed(()=> useCheckMobile().getItem())
const props = defineProps({
        contentStr:{
                type: String,
                default: ""
        },
        valueStr:{
                type: String,
                default: ""
        },
})
const {contentStr,valueStr} = toRefs(props)
</script>
