.sp{cursor: pointer;white-space: pre-line;}
img {display: inline-block;width: 12px;height: 12px;margin-left: 2px;transform: translateY(2px);}
@media screen and (max-width: 1024px) {
        img {display: inline-block;width: 12px;height: 12px;margin-left: 2px;transform: translateY(2px);}
}
</style>